import React from 'react';
import { Button } from 'antd';
import './Countdown.css';
import CountdownDisplay from './CountdownDisplay';
import CountdownInputField from './CountdownInputField';
import CountdownProgress from './CountdownProgress';

export default class Countdown extends React.Component {
  state = {
    minutes: 0,
    seconds: 0,
    totalTime: 0,
    percentTime: 0,
    isPaused: true,
  };

  displayValue = (value) => {
    return ('0' + value.toString()).split('').slice(-2).join('');
  };

  changeValueMin = (min) => {
    this.setState({
      minutes: min,
    });
  };

  changeValueSec = (sec) => {
    this.setState({
      seconds: sec,
    });
  };

  handleStart = () => {
    const totalTime = this.state.minutes * 60 + this.state.seconds;
    this.setState({
      totalTime: totalTime,
    });
    if (this.state.isPaused) {
      this.setState({
        isPaused: false,
      });
      this.timerID = setInterval(() => this.tick(), 1000);
    } else {
      clearInterval(this.timerID);
      this.setState({
        isPaused: true,
      });
    }
  };

  handleReset = () => {
    if (this.timerID) {
      clearInterval(this.timerID);
    }
    this.setState({
      minutes: 0,
      seconds: 0,
      totalTime: 0,
      isPaused: true,
    });
  };

  tick = () => {
    const currentTime = this.state.minutes * 60 + this.state.seconds;
    const newPercentTime = Math.floor(
      (currentTime * 100) / this.state.totalTime
    );
    this.setState({
      percentTime: newPercentTime,
    });
    if (this.state.minutes === 0 && this.state.seconds === 0) {
      clearInterval(this.timerID);
      this.setState({
        isPaused: true,
      });
      return;
    }
    if (this.state.seconds === 0) {
      this.setState({
        seconds: 60,
        minutes: this.state.minutes - 1,
      });
    }
    this.setState({
      seconds: this.state.seconds - 1,
    });
  };

  render() {
    const min = this.displayValue(this.state.minutes);
    const sec = this.displayValue(this.state.seconds);
    const { percentTime } = this.state;

    return (
      <div className="Countdown-container">
        <h1>Счетчик обратного времени</h1>
        <div className="display-progress-container">
          <div className="diasplay-progress-flex-box">
            <CountdownDisplay min={min} sec={sec} />
          </div>
          <div className="diasplay-progress-flex-box">
            <CountdownProgress percentTime={percentTime}/>
          </div>
        </div>
        <div className="InputField-buttons-container">
          <div className="InputField-container">
            <CountdownInputField
              funcMin={this.changeValueMin}
              min={min}
              sec={sec}
              funcSec={this.changeValueSec}
            />
          </div>

          <div className="btn-container">
            <div className="btn">
              <Button type="primary" onClick={this.handleStart}>
                Пуск/пауза
              </Button>
            </div>
            <div className="btn">
              <Button type="primary" onClick={this.handleReset}>
                Сбросить
              </Button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
